@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,500;0,600;0,800;1,100;1,400;1,600;1,800&display=swap");

body {
  margin: 0px;
  padding: 0px;
  background-color: #000000;
  font-family: "Lato", sans-serif;
  max-width: 100%;
  height: 100%;
  width: 100%;
}

.list-style {
  list-style-type: disc;
}
* {
  box-sizing: border-box;
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
}
button {
  outline: none;
  border: none;
}
input {
  outline: none;
  border: none;
}
.logo img {
  width: 180px;
  height: 100%;
}

.particlebg {
  animation: fadeIn 8s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

#main-video {
  margin-top: 50px;
  width: 100%;
}

#main {
  width: 100%;
  height: 768px;

  background-repeat: no-repeat;
  background-size: cover;
  /* background-image: url('./images/bgrt3.png'); */
  background-position-x: center;
  position: relative;
  z-index: 0;
  animation: fadeIn 4s;
  margin-bottom: 40px;
  /* background-color: #d4418e;a-b-img */
  /* background-image: linear-gradient(315deg, #d4418e 0%, #0652c5 74%); */
}

#main::before {
  position: absolute;
  content: "";
  /* background-color: rgb(10, 2, 81); */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#61b50808', endColorstr='#99000000',GradientType=1 );
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}

/* videobg */

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 1;
  background-color: transparent;
  transition: 0.6s ease-in-out;
}
nav.active {
  box-shadow: 5px 10px 30px rgba(0, 0, 0, 0.157);
  background-color: #000000;
}
nav ul {
  display: flex;
}
.active {
  background-color: #ff1414;
  color: #ffffff;
  box-shadow: 5px 10px 30px rgba(198, 64, 64, 0.411);
  transition: all ease 0.2s;
}
.active:hover {
  background-color: #000000;
  color: #ffffff;
  box-shadow: 5px 10px 30px rgba(198, 64, 64, 0.411);
  transition: all ease 0.2s;
}
nav ul li a {
  font-family: "Montserrat";
  font-style: none;
  height: 40px;
  line-height: 43px;
  margin: 3px;
  padding: 0px 22px;
  display: flex;
  font-size: 0.9rem;
  text-transform: uppercase;
  font-weight: 500;
  color: #ffffff;
  letter-spacing: 1px;
  border-radius: 3px;
  transition: 0.2s ease-in-out;
}
nav ul li a:hover {
  /* background-color: #4857f3; */

  text-shadow: 0 0 5px #029af9, 0 0 15px #0361bfed, 0 0 20px #f79d03,
    0 0 40px #ff7300ed, 0 0 60px #ff5900c9, 0 0 10px #ff6200c4, 0 0 98px #ff8c00a2;
  color: #029af9;
  box-shadow: 5px 10px 30px rgb(254, 131, 0);
  transition: all ease 0.2s;
  cursor: pointer;
}

nav .menu-btn,
.menu-icon {
  display: none;
}

.name {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  width: 900px;
  font-size: 0.7rem;
  position: absolute;
  left: 50%;
  top: 17%;
  transform: translate(-50%, -55%);
  text-shadow: 5px 0px 29px #6962dd;
  color: #fcfffc;
  transition: all 0.25s ease-out;
}
.name span {
  color: #ddf332;
  text-shadow: 5px 0px 29px #6962dd;
  color: #fcfffc;
  transition: all 0.25s ease-out;
  font-size: 1rem;
}
.name .details {
  font-size: 23px;
  color: #c5c5c5;
}
.name h2 {
  font-family: "Lato";
  font-size: 2.7rem;
  margin-bottom: 5px;
  letter-spacing: 2px;
  color: #ffffff;
}
.name h1 {
  font-family: "Lato";
  font-size: 3.4rem;
  margin: 7px;
  letter-spacing: 2px;
  padding-bottom: 5px;
  color: #ffffff;
}

#zen {
  padding-top: 25px;
}

.header-btns {
  display: flex;
  margin-top: 40px;
  margin-left: 40%;
}
.header-btn {
  width: 160px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  font-family: "Lato", sans-serif;
  background-color: #ff1414;
  box-shadow: 5px 10px 30px rgba(255, 2, 2, 0.336);
  border-radius: 5px;
  color: #ffffff;
}
.header-btn:hover {
  background-color: transparent;
  transition: all ease 0.5s;
  color: #ffffff;
  border: 2px solid #ff1414;
}

#events {
  margin-top: 850px;
  margin-bottom: 700px;
  width: 100%;
  height: 150vh;
  box-sizing: border-box;
  font-family: "Lato";
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 15%;
  background-color: none;
}
#events h1 {
  color: #fff;
  text-shadow: 0 0 5px #ffa500, 0 0 15px #ffa500, 0 0 20px #ffa500,
    0 0 40px #ffa500, 0 0 60px #ff0000, 0 0 10px #ff8400, 0 0 98px #ff0000;
  font-size: 2rem;
  text-align: center;
}

.event-button {
  background-color: aqua;
  color: black;
  padding: 10px;
  border-radius: 10px;
}

.a-container {
  display: flex;
  justify-content: center;
  align-content: center;
}
.a-box {
  background-color: none;
  width: 360px;
  height: 600px;
  margin: 20px;
  border-radius: 10px;
  overflow: hidden;
  transition: 0.4s ease-in-out;
  position: relative;
}
.a-b-img {
  width: 100%;
  height: 60%;
}
.a-b-img img {
  padding: 15px;
  margin-top: 40px;
  width: 100%;
  height: 100%;
  border-radius: 10px;
}
.a-box:hover {
  border: 1px solid rgba(1, 153, 255, 0.719);
  box-shadow: 2px 2px 12px rgba(255, 153, 1, 0.721);
}
.a-b-text {
  width: 100%;
  height: 60%;
  margin-top: 20px;
  background-color: none;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
}
.a-b-text h2 {
  color: #fff;
}
.a-b-text p {
  visibility: hidden;
}

.a-box:hover .a-b-text p {
  visibility: visible;
  margin: 0px;
  color: #ffffffa6;
  font-size: 1.1rem;
  font-family: "Lato";
  display: block;
  display: -webkit-box;
  max-width: 80%;
  margin-top: 5px;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  margin-bottom: 10px;
}

.a-box:hover {
  transform: translateY(20px);
}

.a-box:hover .details {
  visibility: visible;
  margin: 0px;
  color: #ffffffa6;
  font-size: 1rem;
  font-family: "Lato";
  display: block;
  display: -webkit-box;
  max-width: 80%;
  margin-top: 3px;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  margin-bottom: 2px;
}

.a-b-text p:hover {
  color: #ffffff;
}
.a-b-text a {
  margin-top: 15px;
}

.a-box:hover .a-b-text {
  transform: translateY(-170px);
  transition: 0.4s ease-in-out;
  background-color: #0c0801dc;
}

.a-b-text:hover:before {
  opacity: 1;
}

.details {
  font-family: "Lato";
}

.a-b-button {
  position: relative;
}

.a-box:hover .a-b-button {
  display: flex;
  justify-content: center;
  transform: translateY(-180px);
  transition: 0.4s ease-in-out;
}

